/* fonts */
@import url("https://fonts.googleapis.com/css2?family=Labrada:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;1,100;1,400&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Labrada:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;1,100;1,400&family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;0,900;1,100;1,600&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

